@font-face {
	font-family: 'circe-regular';
	src: url('../fonts/circle/circle-regular/circe-regular.eot?#iefix') format('embedded-opentype'), 
	 url('../fonts/circle/circle-regular/circe-regular.woff') format('woff'),
	 url('../fonts/circle/circle-regular/circe-regular.ttf')  format('truetype'),
	 url('../fonts/circle/circle-regular/circe-regular.svg#circe-regular') format('svg');
	font-weight: normal;
	font-style: normal;
}
@font-face {
	font-family: 'circe-bold';
	src: url('../fonts/circle/circle-bold/circe-bold.eot?#iefix') format('embedded-opentype'), 
	 url('../fonts/circle/circle-bold/circe-bold.woff') format('woff'),
	 url('../fonts/circle/circle-bold/circe-bold.ttf')  format('truetype'),
	 url('../fonts/circle/circle-bold/circe-bold.svg#circe-bold') format('svg');
	font-weight: normal;
	font-style: normal;
}

@font-face {
	font-family: 'circe-light';
	src: url('../fonts/circle/circle-light/circe-light.eot?#iefix') format('embedded-opentype'), 
	 url('../fonts/circle/circle-light/circe-light.woff') format('woff'),
	 url('../fonts/circle/circle-light/circe-light.ttf')  format('truetype'),
	 url('../fonts/circle/circle-light/circe-light.svg#circe-light') format('svg');
	font-weight: normal;
	font-style: normal;
}


.layout{
	flex: 1 0 auto;
	overflow-x: hidden;
}
.page-content{
  overflow: hidden;
}
.layout--ecommerce{
  overflow: visible;
  .page-content{
	  overflow: visible; 
  }
}

.container-fluid{
  max-width: 1920px;
  padding-left: 40px;
  padding-right: 40px;
  position: relative;
  width: 100%;
}

.indent{
  padding-left: calc(8.33333% - 20px);
  padding-right: calc(8.33333% - 20px);
}

.page-title{
  margin-bottom: 35px;
  h1{
	  margin-bottom: 0;
	  
  }
}



.sentence{
  margin-bottom: 70px;
}
.sentence-wrap{
  align-items: flex-start;
}
.sentence-project__wrap{
  display: flex;
  align-items:center;
  color: #C5CDCF;
  &+.sentence-btn{
	  margin-top: 30px;
  }
}
.sentence-project__num{
  font-size: 96px;
  margin-right: 13px;
  line-height: 1;
  white-space: nowrap;
}
.sentence-project__info{

}
.sentence-dealer{
  display: flex;
  align-items: center;
  justify-content: space-between;
  &.m-wrap{
	  flex-wrap:wrap;
	  justify-content: flex-start;
	  margin-bottom: -50px;
	  .dealer-link {
		  margin-bottom: 50px;
		  margin-right: 40px;
	  }
  }
}

.sentence-dealer__slider{
  width: 100%;
  display: none;
  &.slick-initialized{
	  display: block;
  }
  .slick-track{
	  
  }
}
.sentence-dealer__slide{
  display: flex!important;
  align-items: center;
  justify-content: space-between;
}
.dealer-link{
  // margin-right: 96px;
  padding: 0 15px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  position: relative;
  img{
	  display: block;
	  max-width: 100%;
  }
}
.propose{
  background: #F6F4F2;
  border-radius: 8px;
  padding: 15px 12px 15px 95px;
  display: block;
  position: relative;
  color: #141414;
  span{
	  color: #E4002B;
  }
  &:before{
	  content: "";
	  display: inline-block;
	  width: 57px;
	  height: 56px;
	  background: url(../img/svg/plus.svg) no-repeat;
	  background-size: contain;
	  position: absolute;
	  left: 15px;
	  top: 50%;
	  transform: translateY(-50%);
  }
}


.more{
  background: #F6F4F2;
  border-radius: 8px;
  margin-bottom: 43px;
  padding: 33px 0 20px 0;
}
.more__num{
  font-size: 96px;
  line-height: 1;
  margin-bottom: 10px;
}



.news{
  background: #E4002B url(../img/svg/nine.svg) 40px center no-repeat;
  border-radius: 8px;
  padding: 30px 0 40px;
  color: #fff;
  margin-bottom: 40px;
}
.news__wrap{
  align-items: center;
}
.news__list{
  width: 50%;
  padding-right: 15px;
  padding-left: 15px;
  display: flex;
  flex-wrap:wrap;
}
.news__item{
  color: #fff;
  width: 50%;
  margin-bottom: 37px;
  padding-right: 10px;
  &.m-full{
	  padding-right: 0;
	  width: 100%;
  }
}
.news__date{
  font-size: 14px;
  margin-bottom: 10px;
}
.news__title{
  margin-bottom: 13px;
  display: block;
  color: #fff;
}
.news__link{
  background: #FFFFFF;
  border-radius: 3px;
  color: #E4002B;
  padding: 4px 10px 4px 31px;
  display: inline-block;
  position: relative;
  font-size: 14px;
  &:before{
	  content: "";
	  display: inline-block;
	  width: 17px;
	  height: 17px;
	  background: url(../img/svg/link.svg) center no-repeat;
	  position: absolute;
	  left: 8px;
	  top: 50%;
	  transform: translateY(-50%);
  }
}

.news__video{
  width: 50%;
  text-align: right;
  padding-left: 15px;
  padding-right: 15px;
}
.news__video__in{
  text-align: center;
	  display: inline-block;
}
.news__video__img{

  top: 0;
  left: 0;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  &:after{
	  content: "";
	  display: block;
	  width: 100%;
	  height: 100%;
	  position: absolute;
	  left: 0;
	  top: 0;
	  background: rgba(0,0,0,0.5);
  }
  img{
	  display: block;
	  max-width: 100%;
  }
}
.news__video__inner{
	  position: absolute;
  width: 100%;
  height: 100%;
	  display: flex;
  align-items: center;
	  padding: 0 20px;
  text-align: left;
  justify-content: center;
  max-width: 420px;
}
.news-cutting{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 640px;
  height: 445px;
  background: #141414;
  border-radius: 4px;
  color: #fff;
  font-size: 36px;
  font-family: 'circe-bold';
  // padding: 0 100px;
  line-height: 1.1;
  position: relative;
  padding: 45px 5px;
}
.news-cutting__link{
  display: inline-block;
  width: 111px;
  min-width: 111px;
  height: 111px;
  background: url(../img/svg/play.svg) no-repeat;
  margin-right: 20px;
}



.work{
  margin-bottom: 40px;
}
.work__wrap{
  display: flex;
  justify-content: space-between;
}
.work__prev{
  width: 375px;
  min-width: 375px;
}
.work__list{
  padding-left: 170px;
  flex:1;
  display: flex;
  flex-wrap: wrap;
}
.work__item{
  width: 25%;
  display: flex;
  align-items: center;
  margin-bottom: 50px;
  justify-content: center;
