  margin-bottom: 30px;
}
.work__link{
  font-size: 18px;
  line-height: 27px;
  text-transform: uppercase;
  color: #DE4140;
  &:after{
	  content: "";
	  display: inline-block;
	  vertical-align: middle;
	  width: 15px;
	  height: 10px;
	  background: url(../img/svg/r-arr.svg) no-repeat;
	  margin: -5px 0 0 10px;  
  }
