.incut{
  margin-bottom: 40px;
}
.incut__inner{
  max-width: 1045px;
  font-size: 36px;
  line-height: 53px;
}

.process{
  margin-bottom: 120px;
}

.process__wrap{
  display: flex;
}

.process__info{
  width: 375px;
  margin-right: 150px;
}

.process__title{
  font-size: 36px;
  font-family: 'circe-bold';
  line-height: 1.1;
  margin-bottom: 10px;
}

.process-view{
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  flex: 1;
}

.process-view__analitic{
  background: #E4DB00;
  border-radius: 8px;
  width: 100%;
  margin-bottom: 5px;
  padding: 20px 32px; 
  position: relative;
  .process-view__item{
	  padding-right: 50px;
	  position: relative;
	  &:last-child{
		  padding-right: 0;
		  &:after{
			  display: none;
		  }
	  }
	  &:after{
		  content: "";
		  display: inline-block;
		  width: 63px;
		  height: 16px;
		  background: url(../img/svg/right.svg) no-repeat;
		  background-size: contain;
		  position: absolute;
		  right: 20px;
		  top: 9px;
	  }
  }
  &:after{
	  content: "";
	  display: inline-block;
	  width: 82px;
	  height: 168px;
	  background: url(../img/svg/after.svg) no-repeat;
	  background-size: contain;
	  transform: translate(-42px, -4px);
	  position: absolute;
	  left: 100%;
	  top: 50%;
	  z-index: 2;
  }
}

.process-view__develop{
  background: #E4B200;
  border-radius: 8px;
  margin-right: 5px;
  width: 400px;
  height: 100%;
  padding: 16px 32px; 
  position: relative;
  .process-view__list{
	  padding-left: 150px;
	  padding-left: 114px;
	  flex-direction: column;
		  justify-content: space-between;
			  height: calc(100% - 54px);
  }
  &:after{
	  content: "";
	  display: inline-block;
	  width: 82px;
	  height: 168px;
	  background: url(../img/svg/before.svg) no-repeat;
	  background-size: contain;
	  transform: translate(23px, -82px);
	  position: absolute;
	  left: 10px;
	  top: 50%;
	  z-index: 2;
  }
  &.m-mobile{
	  display: none;
  }
}

.process-view__design{
  background: #9BE400;
  border-radius: 8px;
  margin-bottom: 5px;
  width: 100%;
  padding: 13px 58px 25px 64px; 
  position: relative;
  .process-view__item{
	  padding-right: 85px;
	  position: relative;
	  min-width: 50%;
	  &:last-child{
		  padding-right: 0;
		  &:after{
			  display: none;
		  }
	  }
	  &:after{
		  content: "";
		  display: inline-block;
		  width: 63px;
		  height: 16px;
		  background: url(../img/svg/right.svg) no-repeat;
		  background-size: contain;
		  transform: rotate(180deg);
		  position: absolute;
			  right: 20px;
		  top: 10px; 
	  }
  }
  &:before{
	  content: "";
	  display: inline-block;
	  width: 63px;
	  height: 16px;
	  background: url(../img/svg/right.svg) no-repeat;
	  background-size: contain;
	  transform: rotate(180deg) translate(-21px, 50%);
	  position: absolute;
	  right: 100%;
	  top: 50%; 
  }
}

.process-view__start{
  background: #C5CDCF;
  border-radius: 8px;
  width: 100%;
  position: relative;
  padding: 13px 58px 25px 64px;  
  .process-view__item{
	  padding-right: 50px;
	  position: relative;
	  min-width: 50%;
	  &:last-child{
		  padding-right: 0;
		  &:after{
			  display: none;
		  }
	  }
	  &:after{
		  content: "";
		  display: inline-block;
		  width: 63px;
		  height: 16px;
		  background: url(../img/svg/right.svg) no-repeat;
		  background-size: contain;
		  position: absolute;
		  right: 18px;
		  top: 10px;
	  }
  }
  &:before{
	  content: "";
	  display: inline-block;
	  width: 63px;
	  height: 16px;
	  background: url(../img/svg/right.svg) no-repeat;
	  background-size: contain;
	  transform: translate(21px, -50%);
	  position: absolute;
	  right: 100%;
	  top: 50%; 
  }

}

.process-view__title{
  color: #FFFFFF;
  margin-bottom: 10px;
}

.process-view__list{
  display: flex;
}



.process-view__col{
  display: flex;
}

.process-view__complex{
  flex:1;
}

.services{
