.recommend{
  margin-bottom: 40px;
  padding-top: 95px;
}
.recommend-slider-wrap{
  padding-left: 122px;
  overflow: hidden;
  min-width: 0;
  width: 100%;
}

.recommend-slider{
  overflow: hidden;
  .slick-dots{
	  margin: 30px 0 0 0;
	  padding: 0;
	  display: flex;
	  list-style-type: none;
   
	  li{
		  width: 16px;
		  height: 16px;
		  border-radius: 50%;
		  border: 3px solid #fff;
		  background: #C5CDCF;
		  margin-right: 4px;
		  cursor:pointer;
		  transition: background .3s ease;
		  &:last-child{
			  margin-right: 0;
		  }
		  &.slick-active{
			  background: #141414;
			  border: 3px solid #141414;
		  }
		  &:hover{
			  background: #141414;
		  }
		  button{
			  display: none;
		  }
	  }
  }
}

.recommend-slider__img{
  width: 195px;
  min-width: 195px;
  height: 70px;
  overflow: hidden; 
  margin-right: 68px;
  img{
	  display: block;
	  max-width: 100%;
	  max-height: 100%;
	  //object-fit:cover;
  }
}
.recommend-slider__item{
  display: flex!important;
  &.m-single{
	  .recommend-slider__img{
		  width: 132px;
		  min-width: 132px;
		  height: 132px;
		  margin-right: 9px;
		  border-radius: 50%;
		  overflow: hidden;
		  img{
			  width: 100%;
			  height: 100%;
			  object-fit:cover;
		  }
	  }
