}
.recommend-slider__top{
  position: relative;
  margin-bottom: 30px;
  display: flex;
}
.recommend-slider__clutch{
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
}
.recommend-slider__info{
  padding-top: 15px;
}
.recommend-slider__txt{
  font-size: 36px;
  line-height: 53px;
  margin-bottom: 23px;
}
.recommend-slider__link{
  font-size: 18px;
  line-height: 27px;
  text-transform: uppercase;
  color: #E4002B;
  &:after{
	  content: "";
	  display: inline-block;
	  vertical-align: middle;
	  width: 63px;
	  height: 16px;
	  background: url(../img/svg/r-arr-lg.svg) no-repeat;
	  margin: -5px 0 0 10px;
	  transition: all .5s cubic-bezier(.785,.135,.15,.86);
  }
  &:hover{
	  &:after{
		  transform: translate3d(7px,0,0);
	  }
