.roadmap{
  margin-bottom: 40px;
  h2{
		  margin-bottom: 29px;
  }
}

.roadmap__in{
  background: #F6F4F2;
  border-radius: 8px;
  padding: 35px 0 20px;
}

.roadmap__list{
  display: flex;
  margin-right: -30px;
}
.roadmap__item{
  padding-right: 30px;
  width: 33.333%;
  ol{
	  margin: 0;
	  margin-bottom: 20px;
	  padding: 0;
	  counter-reset: list_1;
	  li{
		  list-style-type: none;
		  padding-left: 54px;
		  position: relative;
		  margin-bottom: 15px;
		  font-size: 18px;
		  line-height: 27px;
		  &:before{
			  counter-increment: list_1;
			  content: counters(list_1, ".");
			  position: absolute;
			  left: -1px;
			  top: -1px;
			  color: #fff;
			  font-size: 18px;
			  line-height: 1;
			  width: 27px;
			  min-width: 27px;
			  background: #141414;
			  border-radius: 3px;
			  text-align: center; 
			  height: 27px;
			  display: flex;
			  align-items: center;
			  justify-content: center;
		  }
		  &:last-child{
			  margin-bottom: 0;
		  }
	  }
  }
}
