.support{
  background: #F6F4F2;
  border-radius: 8px;
  padding: 30px; 
  ol{
	  margin: 0;
	  margin-bottom: 20px;
	  padding: 0;
	  counter-reset: list_1;
	  li{
		  list-style-type: none;
		  padding-left: 56px;
		  position: relative;
		  margin-bottom: 15px;
		  font-size: 18px;
		  line-height: 27px;
		  &:before{
			  counter-increment: list_1;
			  content: counters(list_1, ".");
			  position: absolute;
			  left: 2px;
			  top: -2px;
			  color: #fff;
			  font-size: 18px;
			  line-height: 1;
			  width: 27px;
