  background: #F6F4F2;
  border-radius: 8px;
  position: absolute;
  //left: -27px;
  bottom: calc(100% + 20px); 
  color: #141414;
  padding: 15px 27px;
  //width: calc(100% + 54px);
  opacity: 0;
  visibility: hidden;
  transform: translate(-50%, 10px);
  transition: all .3s ease;
  margin-bottom: -10px;
  width: 300px;
  left: 50%;
  &:after{
	  content: "";
	  width: 0;
	  height: 0;
	  border-style: solid;
	  border-width: 20px 18px 0 18px;
	  border-color: #F6F4F2 transparent transparent transparent;
